.modes {
    min-height: 15%;
    min-width: 100%;

    display: -ms-flexbox;

    display: flex;
    -ms-flex-align: center;
        align-items: center;
}

.mode__btn {
    min-height: 100%;
    width: 33.33%;

    display: -ms-flexbox;

    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;

    background: rgba(38, 27, 12, 0.5);

    border-width: 0 0 1px;
    border-color: rgba(38, 27, 12, 0.3);

    color: rgba(255, 214, 204, 1);
    font-size: .8rem;
    font-weight: 700;

    cursor: pointer;

    -webkit-transition: all .5s;

    -o-transition: all .5s;

    transition: all .5s;
}

.mode__btn:hover {
    background: rgba(38, 27, 12, 0.3);
}

@media only screen and (min-width: 576px) {
    .mode__btn {
        font-size: 1.5rem;
    }
}


